import * as React from 'react';
import { StickyContainer, Sticky } from 'react-sticky';
import { ListView, Button, ActionSheet } from 'antd-mobile';
import './TripsList.css';
import TripDTO, { ServiceTargetLocationType } from '../../../dto/TripDTO';
import { WeekDays } from '../../../utils/Constants';
import Tag from 'antd/lib/tag';
import { VehicleType } from '../../../dto/VehicleDTO';
import { message, Avatar } from 'antd';
import TripUsersDTO, { ServiceRoleType } from '../../../dto/TripUsersDTO';
import { compareTripUsers, getRoleBackgroundColor } from '../../../utils/Utils';
import UserDTO from '../../../dto/UserDTO';

export enum TripListType {
  toBook,
  booked
}

interface TripsListState {
  dataSource: any;
}

interface TripsListProps {
  trips: TripDTO[];
  tripListType: TripListType;
  user: UserDTO | undefined;
  onBookTrip?: (trip: TripDTO, roleId: number, userId: number) => void;
  onCancelBookTrip?: (trip: TripDTO) => void;
}

const cancelRoleId: number = 999;

class TripsList extends React.Component<TripsListProps, TripsListState> {
  roles: Array<{ roleId: number; role: string }> = [];

  constructor(props: any) {
    super(props);
    const getSectionData = (dataBlob: any, sectionID: any) => dataBlob[sectionID];
    const getRowData = (dataBlob: any, sectionID: any, rowID: any) => dataBlob[rowID];

    const myDataBlobs = this.buildDataBlobs(props.trips);
    const { dataBlobs, sectionIDs, rowIDs } = myDataBlobs;
    let dataSource = new ListView.DataSource({
      getRowData,
      getSectionHeaderData: getSectionData,
      rowHasChanged: (row1: any, row2: any) => row1 !== row2,
      sectionHeaderHasChanged: (s1: any, s2: any) => s1 !== s2
    });
    dataSource = dataSource.cloneWithRowsAndSections(dataBlobs, sectionIDs, rowIDs);
    this.state = {
      dataSource
    };
  }

  componentWillReceiveProps(nextProps: TripsListProps) {
    const { trips } = nextProps;
    const myDataBlobs = this.buildDataBlobs(trips);
    const { dataBlobs, sectionIDs, rowIDs } = myDataBlobs;
    this.setState({
      dataSource: this.state.dataSource.cloneWithRowsAndSections(dataBlobs, sectionIDs, rowIDs)
    });
  }

  private buildDataBlobs = (trips: TripDTO[]): { dataBlobs: any; sectionIDs: any; rowIDs: any } => {
    let dataBlobs: any = {};
    let sectionIDs: any = [];
    let rowIDs: any = [];
    if (!trips || trips.length === 0) {
      return { dataBlobs, sectionIDs, rowIDs };
    }
    const grouped: any = this.groupBy(trips, 'date');
    const sections: string[] = Object.keys(grouped);
    for (let i = 0; i < sections.length; i++) {
      const date = sections[i];
      sectionIDs.push(date);
      dataBlobs[date] = date;
      rowIDs[i] = [];

      const tripsOfDate: TripDTO[] = grouped[date];
      for (let j = 0; j < tripsOfDate.length; j++) {
        const tripOfDate: TripDTO = tripsOfDate[j];
        const tripId = tripOfDate.id;
        rowIDs[i].push(tripId);
        dataBlobs[tripId] = tripId;
      }
    }
    sectionIDs = [...sectionIDs];
    rowIDs = [...rowIDs];
    return {
      dataBlobs,
      sectionIDs,
      rowIDs
    };
  };

  private groupBy = (trips: TripDTO[], key: string) => {
    if (trips.length === 0) {
      return {};
    }
    return trips.reduce((rv: any, x: any) => {
      (rv[x[key]] = rv[x[key]] || []).push(x);
      return rv;
    }, {});
  };

  private renderSeparator = (sectionID: string | number, rowID: string | number, adjacentRowHighlighted?: boolean): React.ReactElement<any> => (
    <div
      key={`${sectionID}-${rowID}`}
      style={{
        backgroundColor: '#F5F5F9',
        height: 15,
        borderTop: '1px solid #ECECED',
        borderBottom: '1px solid #ECECED'
      }}
    />
  );

  private renderSectionWrapper = (): React.ReactElement<any> => <StickyContainer key={`s_boooo_c`} className="sticky-container" style={{ zIndex: 4 }} />;

  private renderSectionHeader = (sectionData: string, sectionId: string | number): React.ReactElement<any> => {
    const date = new Date(sectionData);
    const weekDay: string = WeekDays[date.getDay()];
    const formattedDate = [this.pad(date.getDate()), this.pad(date.getMonth() + 1), date.getFullYear()].join('/');
    return (
      <Sticky>
        {({ style }) => (
          <div
            className="sticky"
            style={{
              ...style,
              marginTop: '45px',
              zIndex: 4,
              backgroundColor: '#F8591A' /* '#5890ff', */,
              color: 'white'
            }}
          >
            {formattedDate} - {weekDay}
          </div>
        )}
      </Sticky>
    );
  };

  private pad = (s: any) => {
    return s < 10 ? '0' + s : s;
  };

  private renderRow = (rowData: any, sectionID: string | number, rowID: string | number, highlightRow?: boolean): React.ReactElement<any> => {
    const { trips, tripListType } = this.props;
    const trip: TripDTO | undefined = trips.find((x) => x.id === rowID);
    if (trip !== undefined) {
      const { vehicle, trip_users } = trip;
      const tripUsers = (trip_users || []).length;
      const totalUsers = trip.extraUsers + 1;
      const percentage = (tripUsers * 100) / totalUsers;
      return (
        <div key={rowID} style={{ padding: '0 15px' }}>
          <div
            style={{
              textAlign: 'right',
              borderBottom: '2px solid #F6F6F6'
            }}
          >
            <Tag style={{ marginLeft: '5px' }} color="green">
              {ServiceTargetLocationType[trip.flgDestination]}
            </Tag>{' '}
            <b>{trip.hour.substr(0, 5)}</b>
          </div>
          <div
            style={{
              textAlign: 'left',
              borderBottom: '2px solid #F6F6F6'
            }}
          >
            <b>Partenza:</b> {trip.siteDeparture}
          </div>
          <div
            style={{
              textAlign: 'left',
              borderBottom: '2px solid #F6F6F6'
            }}
          >
            <b>Arrivo:</b> {trip.siteArrival}
          </div>
          <div
            style={{
              textAlign: 'left',
              borderBottom: '2px solid #F6F6F6'
            }}
          >
            <b>Veicolo: </b> {!!vehicle ? `${VehicleType[vehicle.type]} - ${vehicle.sign} - ${vehicle.description}` : '-'}
          </div>
          <div
            style={{
              textAlign: 'left'
            }}
          >
            <div>
              <b>Servizio:</b> {trip.serviceType}
            </div>
            <div>
              <b>Note:</b> {trip.notes}
            </div>
          </div>
          <div
            style={{
              textAlign: 'right'
            }}
          >
            N posti disponibili{' '}
            <Tag color={percentage >= 50 ? 'orange' : 'green'} onClick={() => this.showBookingUsers(trip)}>
              {(trip_users || []).length}/<b>{trip.extraUsers + 1}</b>
            </Tag>
          </div>
          {tripListType === TripListType.toBook ? (
            <Button style={{ marginTop: '5px' }} type="primary" icon="check-circle" size="small" onClick={() => this.showBookingRoles(trip)}>
              Prenota
            </Button>
          ) : (
            <Button style={{ marginTop: '5px' }} type="warning" icon="cross-circle-o" size="small" onClick={() => this.cancelBookTrip(trip)}>
              Annulla prenotazione
            </Button>
          )}
        </div>
      );
    } else {
      return <div>Data not loaded!</div>;
    }
  };

  private cancelBookTrip = (trip: TripDTO) => {
    const { onCancelBookTrip } = this.props;
    if (onCancelBookTrip !== undefined) {
      onCancelBookTrip(trip);
    }
  };

  private showBookingUsers = (trip: TripDTO) => {
    const tripUsers: TripUsersDTO[] = trip.trip_users || [];
    if (tripUsers.length === 0) {
      return;
    }

    const shareOptions: any[] = tripUsers.sort(compareTripUsers).map((tripUser: TripUsersDTO) => {
      return {
        icon: <Avatar style={{ backgroundColor: getRoleBackgroundColor(tripUser.role) }} shape="square" icon="user" />,
        title: (
          <span>
            {tripUser.user?.name || ''} {tripUser.user?.surname || ''} <b>({ServiceRoleType[tripUser.role]})</b>
          </span>
        )
      };
    });

    const config: any = {
      options: shareOptions,
      title: 'Utenti prenotati al servizio',
      className: 'my-saswo-class',
      cancelButtonText: 'Chiudi'
    };
    ActionSheet.showShareActionSheetWithOptions(config);
  };

  private showBookingRoles = (trip: TripDTO) => {
    const { user } = this.props;
    const { trip_users } = trip;
    if (trip.extraUsers + 1 === trip_users?.length) {
      message.info('Servizio al completo');
      return;
    }

    this.roles = [];
    this.roles.push({ roleId: 1, role: 'Soccorritore' });
    if (user !== undefined) {
      if (user.withDrivingLicense > 0) {
        this.roles = [{ roleId: 0, role: 'Autista' }, ...this.roles];
      }
      const { committeeUser } = user;
      const { occupation } = committeeUser;
      if (occupation === 2 && trip.flgNurse > 0) {
        this.roles = [...this.roles, { roleId: 2, role: 'Medico/Sanitario' }];
      }
    }
    this.roles = [...this.roles, { roleId: cancelRoleId, role: 'Annulla' }];
    const options = this.roles.map((element: { roleId: number; role: string }) => element.role);
    ActionSheet.showActionSheetWithOptions(
      {
        options,
        destructiveButtonIndex: options.length - 1,
        message: 'Seleziona il ruolo',
        maskClosable: true
      },
      (index: number, rowIndex?: number) => {
        if (index === -1) {
          return;
        }
        const { roleId } = this.roles[index];
        if (roleId === cancelRoleId) {
          return;
        }
        const { onBookTrip } = this.props;
        if (onBookTrip !== undefined) {
          onBookTrip(trip, roleId, user?.id || 0);
        }
      }
    );
  };

  private renderFooter = (): React.ReactElement<any> => (
    <div></div>
    // <div style={{ padding: 30, textAlign: 'center' }}>{this.state.isLoading ? 'Caricamento in corso...' : ''}</div>
  );

  public render() {
    const { dataSource } = this.state;
    return (
      <ListView
        dataSource={dataSource}
        className="am-list sticky-list"
        useBodyScroll
        renderSectionWrapper={this.renderSectionWrapper}
        renderSectionHeader={this.renderSectionHeader}
        renderFooter={this.renderFooter}
        renderRow={this.renderRow}
        renderSeparator={this.renderSeparator}
        onScroll={() => {
          console.log('scroll');
        }}
        scrollEventThrottle={200}
      />
    );
  }
}

export default TripsList;
