import React from 'react';
import { Route } from 'react-router-dom';
import { TabBar, NavBar, Icon as IconMobile, Popover, Modal } from 'antd-mobile';
import { connect } from 'react-redux';
import { Icon, message } from 'antd';
import { PORTAL_NAME } from '../../utils/Constants';
import { Links } from '../../utils/Links';
import { WithUser, WithCommittee, WithConnector } from '../../reducers/reducers';
import { ResetAuthDataProps, resetAuthData } from '../../actions/action';
import { RouterProps } from 'react-router';
import TripDTO from '../../dto/TripDTO';
import PersonalAreaMobileContainer from './PersonalAreaMobile';
import TripUsersDTO from '../../dto/TripUsersDTO';
import { filterTripsByOccupation } from '../../utils/Utils';
import TripsList, { TripListType } from './base/TripsList';

const alert = Modal.alert;
const Item = Popover.Item;

enum RightTopMenuChoise {
  Logout = '1'
}

interface DashboardMobileState {
  selectedTab: 'servicesTab' | 'bookedServicesTab' | 'personalAreaTab';
  hidden: boolean;
  rightTopMenuVisible: boolean;
  isLoading: boolean;
  availableTrips: TripDTO[];
  bookedTrips: TripDTO[];
}

interface DashbardMobileProps extends WithUser, WithCommittee, WithConnector, ResetAuthDataProps, RouterProps {}

class DashboardMobile extends React.Component<DashbardMobileProps, DashboardMobileState> {
  constructor(props: any) {
    super(props);

    const { user, committee } = this.props;
    if (!user && !committee) {
      this._onLogout();
    }

    this.state = {
      selectedTab: 'servicesTab',
      hidden: false,
      rightTopMenuVisible: false,
      isLoading: true,
      availableTrips: [],
      bookedTrips: []
    };
  }

  componentDidMount() {
    this.loadData();
  }

  private loadData = () => {
    this.setState({ isLoading: true }, () => {
      const { connector, user } = this.props;
      let allTrips: [Promise<any>, Promise<any>] = [connector.getAvailableTrips(), connector.getBookedTrips()];
      Promise.all(allTrips)
        .then((results: any[]) => {
          const availableTrips: TripDTO[] = (results[0] || []).data
            .filter((trip: TripDTO) => trip.extraUsers + 1 !== trip.trip_users?.length)
            .filter((trip: TripDTO) => filterTripsByOccupation(trip, user));
          const bookedTrips: TripDTO[] = results[1].data || [];
          this.setState({
            availableTrips,
            bookedTrips,
            isLoading: false
          });
        })
        .catch((err: any) => {
          console.error(err);
          this.setState({ availableTrips: [], bookedTrips: [], isLoading: false });
        });
    });
  };

  _onLogout = () => {
    this.props.history.replace({
      pathname: Links.LOGOUT,
      search: '?layout=mobile'
    });
  };

  renderTabContent() {
    if (this.state.isLoading) {
      return <div style={{ textAlign: 'center', marginTop: '10px' }}>Caricamento in corso...</div>;
    }
    return (
      <div style={{ height: '100%', textAlign: 'center' }}>
        <Route
          path={Links.MOBILE_SERVICES}
          component={() => (
            <TripsList trips={this.state.availableTrips} tripListType={TripListType.toBook} user={this.props.user} onBookTrip={this._onBookTrip} />
          )}
        />
        <Route
          path={Links.MOBILE_BOOKED_SERVICES}
          component={() => (
            <TripsList trips={this.state.bookedTrips} tripListType={TripListType.booked} user={this.props.user} onCancelBookTrip={this._onCancelBookTrip} />
          )}
        />
        <Route path={Links.MOBILE_PERSONAL_AREA} exact={true} component={PersonalAreaMobileContainer} />
      </div>
    );
  }

  private _onCancelBookTrip = (trip: TripDTO): void => {
    alert('Attenzione', `Sicuro di voler annullare la prenotazione per il viaggio selezionato?`, [
      {
        text: 'Annulla',
        onPress: () => {
          /* Do nothing */
        }
      },
      { text: 'Ok', onPress: () => this._onClickCancelBookTrip(trip) }
    ]);
  };

  private _onClickCancelBookTrip = (trip: TripDTO): void => {
    const { user } = this.props;
    const tripUserToDelete: TripUsersDTO | undefined = (trip.trip_users || []).find((tripUser: TripUsersDTO) => tripUser.userId === user?.id);
    if (tripUserToDelete !== undefined) {
      const tripUserId: number = tripUserToDelete.id;
      this.props.connector
        .deleteTripUsers(tripUserId)
        .then((res) => {
          this.loadData();
        })
        .catch((err) => {
          console.error(`_onDeleteTripUser: ${err}`);
          message.error(`Si  verificato un errore nell'eliminazione`);
        });
    } else {
      message.error(`Si  verificato un errore nella richiesta`);
    }
  };

  private _onBookTrip = (trip: TripDTO, role: number, userId: number): void => {
    const tripToPublish: TripUsersDTO = {
      id: 0,
      tripId: trip.id,
      userId,
      role
    };
    this.setState({ isLoading: true });
    this.props.connector
      .publishTripUser(tripToPublish)
      .then((res) => {
        message.success('Prenotazione effettuata con successo');
        this.setState({ isLoading: false }, () => {
          this.loadData();
        });
      })
      .catch((err) => {
        console.log(err);
        let errorMessage = 'Si  verificato un errore nella richiesta';
        if (err.response && err.response.data && err.response.data.message) {
          errorMessage = err.response.data.message;
        }
        this.setState({ isLoading: false }, () => {
          message.error(errorMessage);
        });
      });
  };

  private _onClickLogout = () => {
    this.props.resetAuthData();
    this._onLogout();
  };

  private _onSelectRightTopMenu = (node: any, index?: number) => {
    switch (node.key) {
      case RightTopMenuChoise.Logout:
        alert('Attenzione', `Sicuro di voler uscire?`, [
          {
            text: 'Annulla',
            onPress: () => {
              /* Do nothing */
            }
          },
          { text: 'Ok', onPress: () => this._onClickLogout() }
        ]);
        break;
      default:
        break;
    }
    this.setState({
      rightTopMenuVisible: false
    });
  };

  private _rightTopMenuVisibleChange = (rightTopMenuVisible: boolean) => {
    this.setState({
      rightTopMenuVisible
    });
  };

  public render() {
    const { committee } = this.props;
    return (
      <div>
        <NavBar
          mode="dark"
          leftContent={<span style={{ fontSize: '12px', fontWeight: 'bold' }}>{PORTAL_NAME}</span>}
          rightContent={[
            <Icon key="0" type="reload" style={{ marginRight: '5px' }} onClick={this.loadData} />,
            <Popover
              key="1"
              visible={this.state.rightTopMenuVisible}
              overlay={[<Item key={RightTopMenuChoise.Logout}>Logout</Item>]}
              onVisibleChange={this._rightTopMenuVisibleChange}
              onSelect={this._onSelectRightTopMenu}
            >
              <div
                style={{
                  height: '100%',
                  padding: '0 15px',
                  marginRight: '-15px',
                  display: 'flex',
                  alignItems: 'center'
                }}
              >
                <IconMobile type="ellipsis" />
              </div>
            </Popover>
          ]}
        >
          <span style={{ fontSize: '12px' }}>{committee?.description}</span>
        </NavBar>
        <div style={{ position: 'fixed', height: 'calc(100% - 45px)', width: '100%', top: 45 }}>
          <TabBar
            unselectedTintColor="#949494"
            tintColor="#33A3F4"
            barTintColor="white"
            tabBarPosition="bottom"
            hidden={this.state.hidden}
            prerenderingSiblingsNumber={0}
          >
            <TabBar.Item
              title="Servizi disponibili"
              key="Servizi"
              icon={<Icon type="calendar" />}
              selectedIcon={<Icon type="calendar" />}
              selected={this.state.selectedTab === 'servicesTab'}
              badge={this.state.availableTrips.length}
              onPress={() => {
                this.setState(
                  {
                    selectedTab: 'servicesTab'
                  },
                  () => this.props.history.replace(Links.MOBILE_SERVICES)
                );
              }}
              data-seed="logId"
            >
              {this.renderTabContent()}
            </TabBar.Item>
            <TabBar.Item
              title="Servizi prenotati"
              key="booked_services"
              icon={<Icon type="book" />}
              selectedIcon={<Icon type="book" />}
              selected={this.state.selectedTab === 'bookedServicesTab'}
              badge={this.state.bookedTrips.length}
              onPress={() => {
                this.setState(
                  {
                    selectedTab: 'bookedServicesTab'
                  },
                  () => this.props.history.replace(Links.MOBILE_BOOKED_SERVICES)
                );
              }}
              data-seed="logId1"
            >
              {this.renderTabContent()}
            </TabBar.Item>
            <TabBar.Item
              title="Area personale"
              key="personal_area"
              icon={<Icon type="user" />}
              selectedIcon={<Icon type="user" />}
              selected={this.state.selectedTab === 'personalAreaTab'}
              onPress={() => {
                this.setState(
                  {
                    selectedTab: 'personalAreaTab'
                  },
                  () => this.props.history.replace(Links.MOBILE_PERSONAL_AREA)
                );
              }}
              data-seed="logId2"
            >
              {this.renderTabContent()}
            </TabBar.Item>
          </TabBar>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state: any, ownProps: any) => {
  return {
    user: state.appData.user,
    committee: state.appData.committee,
    connector: state.appData.connector
  };
};

const DashboardMobileContainer = connect(mapStateToProps, { resetAuthData })(DashboardMobile);
export default DashboardMobileContainer;
